export const register = (email, password, name, phoneNum) => {
  users.push({
    email: email,
    password: password,
    name: name,
    phoneNum: phoneNum,
  });
};

export const singIn = ({ email, password }) => {
  const user = users.find(
    (user) => user.email === email && user.password === password,
  );

  if (user === undefined) throw new Error();
  return user;
};
