    //           <p>{list.totalPrice}</p>
    //           <GrFormClose onClick={() => onCloseBtn(idx)} />
    //         </div>
    //       ))}
    //     </div>
    //     <hr />
    //     <div className="main-addMenu">
    //       <p onClick={onAddMenu}>+</p>
    //     </div>
    //   </div>
    //   <hr />
    //   <div className="cart-coupon">
    //     <p className="coupon-title"></p>
    //     <div className="coupon-card">
    //       <p className="card-title">  0</p>
    //       <div className="coupon-inner">
    //         <div>
    //           <p>  </p>
    //         </div>
    //         <button></button>
    //       </div>
    //     </div>
    //   </div>
    //   <hr />
    //   <div className="cart-list">
    //     <div>
    //       <p></p>
    //       <p>{payment}</p>
    //     </div>
    //     <div>
    //       <p></p>
    //       <p>{detail.deliveryCost}</p>
    //     </div>
    //     <div>
    //       <p className="list-amount"> </p>
    //       <p>{detail.deliveryCost + payment}</p>
    //     </div>
    //   </div>
    //   <hr />

    //   <div className="cart-request">
    //     <p className="request-header"></p>
    //     <div>
    //       <p> </p>
    //       <textarea placeholder=")  "></textarea>
    //       <input type="checkbox" />
    //       <label htmlFor="request"> ,  </label>
    //     </div>
    //     <div>
    //       <p> </p>
    //       <textarea placeholder="   "></textarea>
    //     </div>
    //     <hr />

    //     <div>
    //       <p></p>
    //       <p>KB ********</p>
    //     </div>
    //   </div>
    //   <div className="cart-payment">
    //     <button onClick={onPayBtn}>
    //       {detail.deliveryCost + payment} 
