import React from 'react';
import { Redirect, Route } from 'react-router-dom';

const AuthRoute = ({
  authenticated,
  component: Component,
  render,
  ...rest
}) => {
  return (
    <Route
      {...rest}
      render={(props) =>
        authenticated ? (
          // authenticated === true
          render ? (
            // render  ,   render props   ?
            render(props)
          ) : (
            //   render props   ProfilePage 
            <Component {...props} />
          )
        ) : (
          // authenticated === false
          <Redirect
            to={{ pathname: '/login', state: { from: props.location } }}
          />
        )
      }
    />
  );
};

export default AuthRoute;
