import React from 'react';
import LogoutButton from '../compoentns/LogoutButton';
import './SettingPage.scss';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

const SettingPage = ({ logout, history }) => {
  const handleClick = () => {
    logout();
    history.push('/');
  };

  return (
    <Container className="setting">
      <Row>
        <Col>
          <h1 className="setting-title"></h1>
        </Col>
      </Row>
      <Row>
        <Col>
          <div className="setting-list">  </div>
        </Col>
      </Row>
      <Row>
        <Col>
          <div className="setting-list">&amp;  </div>
        </Col>
      </Row>
      <Row>
        <Col>
          <div className="setting-list" onClick={handleClick}>
            
          </div>
        </Col>
      </Row>
    </Container>
    // <div>
    //   <h1> </h1>
    //   <div>  </div>
    //   <div>&amp;  </div>
    //   <LogoutButton logout={logout} />
    // </div>
  );
};

export default SettingPage;
